import React, {useState} from 'react'
import {Input, Select, Space} from 'antd'
import Title from '../Text/Title'
import StyledButton from '../Button/StyledButton'

const PlayerSearch = ({
  onSearch = (searchType: string, searchValue: string) => {},
  onlyPid = false,
}) => {
  const [searchType, setSearchType] = useState('pid')
  const [searchValue, setSearchValue] = useState('')
  return (
    <Space>
      <Title style={{marginRight: 15}}>유저 검색</Title>
      <Select
        style={{width: 150}}
        defaultValue={'pid'}
        value={searchType}
        onChange={(v) => setSearchType(v)}
      >
        <Select.Option value={'pid'}>플레이어 PID</Select.Option>
        {!onlyPid && <Select.Option value={'nickname'}>닉네임</Select.Option>}
      </Select>
      <Input
        placeholder={'입력하세요.'}
        value={searchValue}
        onChange={(e) => setSearchValue(e.target.value)}
      />
      <StyledButton blue onClick={() => onSearch(searchType, searchValue)}>
        검색
      </StyledButton>
    </Space>
  )
}

export default PlayerSearch
