import {makeAutoObservable} from 'mobx'

export default class ChannelModel {
  store: any
  key: string
  number: number
  _id: string
  wid: string
  status: string
  playerNum: number
  constructor(store: any, {_id, playerNum, wid, status}: any, number: number) {
    makeAutoObservable(this)
    this.store = store
    this._id = _id
    this.number = number
    this.key = _id
    this.wid = wid
    this.status = status
    this.playerNum = playerNum
  }
}

export const columns = []
