import React, {useState} from 'react'
import {observer} from 'mobx-react-lite'
import BasicLayout from '../../components/Layout/BasicLayout'
import TitlePanel from '../../components/Layout/TitlePanel'
import Content from '../../components/Layout/Content'
import ServerSearch from '../../components/Search/ServerSearch'
import BasicTabs from '../../components/Tabs/Tabs'
import Title from '../../components/Text/Title'
import {DatePicker, Form, Input, Select} from 'antd'
import StyledButton from '../../components/Button/StyledButton'
import {sendMail} from '../../apis/player'

const MailPage = () => {
  const [channel, setChannel] = useState('')

  const send = (values: any) => {
    const {pid, ...restValues} = values
    return sendMail({
      pids: pid,
      content: restValues,
    }).then(() => {
      alert('우편 발송 완료!')
    })
  }
  return (
    <BasicLayout>
      <TitlePanel>메시지 관리</TitlePanel>
      <Content>
        <ServerSearch onChange={(w: any, c: any) => c && setChannel(c._id)} />
        <Title style={{marginTop: 40}}>우편 발송 및 조회</Title>
        <BasicTabs tabs={['우편 발송']}>
          <div style={{width: 800}}>
            <Form
              requiredMark={false}
              colon={false}
              labelCol={{xs: 5}}
              wrapperCol={{xs: 19}}
              onFinish={send}
              initialValues={{target_type: 'one'}}
            >
              <div style={{textAlign: 'right', marginBottom: 20}}>
                <StyledButton blue>우편 발송</StyledButton>
              </div>
              <Form.Item
                label={'제목'}
                name={'title'}
                style={{marginBottom: 10}}
                rules={[{required: true, message: '제목을 입력해주세요.'}]}
              >
                <Input />
              </Form.Item>
              <Form.Item
                label={'대상'}
                name={'target_type'}
                style={{marginBottom: 10}}
              >
                <Select>
                  <Select.Option value={'one'}>특정 유저</Select.Option>
                </Select>
              </Form.Item>
              <Form.Item
                label={'유저 ID'}
                name={'pid'}
                style={{marginBottom: 10}}
              >
                <Input />
              </Form.Item>
              <Form.Item
                label={'시간'}
                name={'createdAt'}
                style={{marginBottom: 10}}
              >
                <DatePicker />
              </Form.Item>
              <Form.Item
                label={'상세 내용'}
                name={'content'}
                style={{marginBottom: 30}}
              >
                <Input.TextArea />
              </Form.Item>
            </Form>
          </div>
          <div></div>
        </BasicTabs>
      </Content>
    </BasicLayout>
  )
}

export default observer(MailPage)
