import React, {useState} from 'react'
import {observer} from 'mobx-react-lite'
import {Tabs, Divider, Space, Pagination, Popover} from 'antd'
import BasicLayout from '../../components/Layout/BasicLayout'
import TitlePanel from '../../components/Layout/TitlePanel'
import Content from '../../components/Layout/Content'
import Panel from '../../components/Layout/Panel'
import ServerSearch from '../../components/Search/ServerSearch'
import PlayerSearch from '../../components/Search/PlayerSearch'
import Title from '../../components/Text/Title'
import StyledButton from '../../components/Button/StyledButton'
import {useStores} from '../../stores'
import PlayerDesc from '../../components/Description/PlayerDesc'
import {PlayerType} from '../../stores/models/PlayerModel'
import MailForm from '../../components/Form/MailForm'
import {CloseSquareOutlined, MailOutlined} from '@ant-design/icons'
import Resources from '../../components/player/Resources'
import Items from '../../components/player/Items'
import Equipments from 'components/player/Equipments'
import Heros from '../../components/player/Heros'
import Adventures from '../../components/player/Adventures'
import Agit from 'components/player/Agit'

const AdminUserPage = () => {
  const [visible, setVisible] = useState(false)
  const {
    playerStore: {
      currentPlayer,
      playerList,
      setCurrentPlayer,
      setIds,
      getPlayer,
      getPlayerList,
    },
  } = useStores()

  return (
    <BasicLayout>
      <TitlePanel>유저 관리</TitlePanel>
      <Panel>
        <ServerSearch />
        <Divider />
        <PlayerSearch
          onSearch={(searchType, searchValue) => {
            if (searchType === 'pid') {
              getPlayer({pid: searchValue})
            }
            if (searchType === 'nickname') {
              getPlayerList('', 1, {nickname: searchValue})
            }
          }}
        />
        {currentPlayer && (
          <div style={{marginTop: 30}}>
            <Space>
              <Title style={{marginRight: 15}}>기본 정보</Title>
              <Popover
                placement="bottomRight"
                content={
                  <div
                    style={{
                      width: 600,
                      border: '2px solid #bababb',
                    }}
                  >
                    <div
                      style={{
                        background: '#fafafa',
                        display: 'flex',
                        padding: '10px 20px 0',
                        alignItems: 'center',
                        justifyContent: 'space-between',
                      }}
                    >
                      <Space align={'baseline'}>
                        <MailOutlined
                          style={{
                            fontSize: 20,
                            color: '#a7a7a7',
                            marginRight: 5,
                          }}
                        />
                        <h2>우편 보내기</h2>
                      </Space>
                      <span>
                        <CloseSquareOutlined
                          onClick={() => setVisible(false)}
                          style={{fontSize: 20, color: '#a7a7a7'}}
                        />
                      </span>
                    </div>
                    <div style={{padding: '0 16px 20px'}}>
                      <MailForm
                        players={[currentPlayer]}
                        onClose={() => setVisible(false)}
                      />
                    </div>
                  </div>
                }
                trigger="click"
                visible={visible}
                onVisibleChange={setVisible}
              >
                <StyledButton purple>우편 보내기</StyledButton>
              </Popover>
              <StyledButton
                gray
                onClick={() =>
                  currentPlayer.changePlayerType(PlayerType.계정정지)
                }
              >
                계정 정지
              </StyledButton>
              <StyledButton
                gray
                onClick={() =>
                  currentPlayer.changePlayerType(PlayerType.계정해지)
                }
              >
                계정 해지
              </StyledButton>
              <StyledButton
                yellow
                onClick={() => currentPlayer.changePlayerType(PlayerType.VIP)}
              >
                VIP 지정
              </StyledButton>
              <StyledButton
                yellow
                onClick={() => currentPlayer.changePlayerType('')}
              >
                VIP 해제
              </StyledButton>
            </Space>
            <div style={{marginTop: 30}}>
              <PlayerDesc player={currentPlayer} />
              <div style={{textAlign: 'center', marginTop: 20}}>
                <Pagination
                  size={'small'}
                  total={playerList.length}
                  onChange={(page: number) => setCurrentPlayer(page)}
                />
              </div>
            </div>
          </div>
        )}
      </Panel>
      {currentPlayer && (
        <Content>
          <Tabs type={'card'} style={{marginTop: 50}}>
            <Tabs.TabPane tab="재화" key="재화">
              <Resources userResources={currentPlayer.userResources} />
            </Tabs.TabPane>
            <Tabs.TabPane tab="아이템" key="아이템">
              <Items items={currentPlayer.gameItems} />
            </Tabs.TabPane>
            <Tabs.TabPane tab="장비" key="장비">
              <Equipments items={currentPlayer.equipments} />
            </Tabs.TabPane>
            <Tabs.TabPane tab="영웅" key="영웅">
              <Heros items={currentPlayer.heros} />
            </Tabs.TabPane>
            <Tabs.TabPane tab="모험" key="모험">
              <Adventures items={[currentPlayer.adventures]} />
            </Tabs.TabPane>
            <Tabs.TabPane tab="던전" key="던전">
              <Agit items={currentPlayer.agit} />
            </Tabs.TabPane>
          </Tabs>
        </Content>
      )}
    </BasicLayout>
  )
}

export default observer(AdminUserPage)
