import React, {useEffect, useState} from 'react'
import {observer} from 'mobx-react-lite'
import {Table} from 'antd'
import BasicLayout from '../../components/Layout/BasicLayout'
import TitlePanel from '../../components/Layout/TitlePanel'
import Content from '../../components/Layout/Content'
import ServerSearch from '../../components/Search/ServerSearch'
import BasicTabs from '../../components/Tabs/Tabs'
import Title from '../../components/Text/Title'
import {useStores} from '../../stores'

const RankPage = () => {
  const [activeKey, setActiveKey] = useState('계정 레벨')
  const {
    playerStore: {
      wid,
      cid,
      setIds,
      getPlayerList,
      playerList,
      pageInfo,
      playerCount,
    },
  } = useStores()
  const columns = [
    {title: '랭킹', dataIndex: 'number'},
    {title: '레벨', dataIndex: 'userLevel'},
    {title: '유저 ID', dataIndex: 'pid'},
    {title: '닉네임', dataIndex: 'nickname'},
    {title: '전투력', dataIndex: 'userBattlePower'},
    {title: '생성 날짜', dataIndex: 'createdAt'},
    {title: '최근 접속 일시', dataIndex: 'rank'},
    {title: '비고', dataIndex: ''},
  ]

  useEffect(() => {
    if (wid && cid) {
      getPlayerList(undefined, 1, {
        sort: {'profile.userLevel': -1},
      })
    }
  }, [wid, cid])
  return (
    <BasicLayout>
      <TitlePanel>데이터 관리</TitlePanel>
      <Content>
        <ServerSearch onChange={setIds} />
        <Title style={{marginTop: 100}}>게임 랭킹 조회</Title>
        <BasicTabs
          onChange={(activeKey: string) => {
            let params = {}
            switch (activeKey) {
              case '계정 레벨':
                params = {
                  sort: {'profile.userLevel': -1},
                }
                break
              case '전투력':
                params = {
                  sort: {'profile.userBattlePower': -1},
                }
                break
            }
            setActiveKey(activeKey)
            getPlayerList(undefined, 1, params)
          }}
          style={{marginTop: 10}}
          tabs={['계정 레벨', '전투력', '모험 진행', '투기장(PVP)', '던전']}
        />
        <div>
          <Table
            bordered
            columns={columns}
            dataSource={playerList}
            pagination={{
              position: ['bottomCenter'],
              total: playerCount,
              pageSize: pageInfo.size,
              onChange: (page) => {
                getPlayerList(activeKey, page)
              },
            }}
          />
        </div>
      </Content>
    </BasicLayout>
  )
}

export default observer(RankPage)
