import React, {useEffect, useState} from 'react'
import {observer} from 'mobx-react-lite'
import {
  Form,
  Row,
  Col,
  DatePicker,
  Input,
  Select,
  Space,
  Tabs,
  Table,
} from 'antd'
import BasicLayout from '../../components/Layout/BasicLayout'
import TitlePanel from '../../components/Layout/TitlePanel'
import Content from '../../components/Layout/Content'
import StyledButton from '../../components/Button/StyledButton'
import {useStores} from '../../stores'
import AdminUserModel from '../../stores/models/AdminUserModel'

const AdminUserPage = () => {
  const [form] = Form.useForm()
  const [selectedRowModels, setSelectedRowModels] = useState<AdminUserModel[]>(
    []
  )
  const {
    adminStore: {adminUserList, getAdminUserList, createAdminUser},
  } = useStores()
  const columns = [
    {title: '넘버', dataIndex: 'number'},
    {title: 'ID', dataIndex: '_id'},
    {title: 'PW', dataIndex: 'pwd'},
    {title: '소속', dataIndex: 'part'},
    {title: '생성 날짜', dataIndex: 'createdAt'},
    {title: '최근 접속 일시', dataIndex: 'lastLoggedInAt'},
    {
      title: '관리자 등급',
      dataIndex: 'grade',
      render: (value: string) => (value === 'Main' ? '메인' : '서브'),
    },
    {title: '비고', dataIndex: ''},
  ]

  const onFinish = (type: string) => {
    form.validateFields().then((values) => {
      createAdminUser(type, values)
        .then(() => getAdminUserList())
        .then(() => form.resetFields())
    })
  }

  const onRemove = () => {
    return Promise.all(selectedRowModels.map((model) => model.remove())).then(
      () => {
        getAdminUserList()
      }
    )
  }

  useEffect(() => {
    getAdminUserList()
  }, [])
  return (
    <BasicLayout>
      <TitlePanel>관리자 회원 관리</TitlePanel>
      <Content title={'관리자 계정 생성'}>
        <Form
          colon={false}
          form={form}
          style={{paddingLeft: 20}}
          initialValues={{country: 'korea'}}
        >
          <Row gutter={32}>
            <Col>
              <Form.Item
                label={'생성일'}
                name={'createdAt'}
                style={{marginBottom: 10}}
              >
                <DatePicker />
              </Form.Item>
            </Col>
            <Col>
              <Form.Item
                label={'ID'}
                name={'_id'}
                style={{marginBottom: 10}}
                rules={[{required: true, message: 'ID를 입력해주세요.'}]}
              >
                <Input />
              </Form.Item>
            </Col>
            <Col>
              <Form.Item
                label={'PW'}
                name={'pwd'}
                style={{marginBottom: 10}}
                rules={[{required: true, message: 'PW를 입력해주세요.'}]}
              >
                <Input />
              </Form.Item>
            </Col>
            <Col>
              <Form.Item
                label={'소속'}
                name={'part'}
                style={{marginBottom: 10}}
                rules={[{required: true, message: '소속을 입력해주세요.'}]}
              >
                <Input />
              </Form.Item>
            </Col>
            <Col>
              <Form.Item
                label={'국가'}
                name={'country'}
                style={{marginBottom: 10}}
              >
                <Select style={{width: 150}}>
                  <Select.Option value="korea">한국</Select.Option>
                </Select>
              </Form.Item>
            </Col>
          </Row>
          <Space style={{marginLeft: 46}}>
            <StyledButton purple onClick={() => onFinish('Main')}>
              +메인 계정 생성
            </StyledButton>
            <StyledButton blue onClick={() => onFinish('Sub')}>
              +서브 계정 생성
            </StyledButton>
          </Space>
        </Form>
        <Tabs type={'card'} style={{marginTop: 50}}>
          <Tabs.TabPane tab="관리자" key="1">
            <StyledButton gray onClick={onRemove} style={{marginBottom: 10}}>
              계정 삭제
            </StyledButton>
            <Table
              bordered
              columns={columns}
              dataSource={adminUserList}
              pagination={{position: ['bottomCenter']}}
              rowSelection={{
                hideSelectAll: true,
                onChange: (
                  selectedRowKeys: React.Key[],
                  selectedRowModels: AdminUserModel[]
                ) => {
                  setSelectedRowModels(selectedRowModels)
                },
              }}
            />
          </Tabs.TabPane>
        </Tabs>
      </Content>
    </BasicLayout>
  )
}

export default observer(AdminUserPage)
