import React from 'react'
import {Tabs} from 'antd'
import styled from 'styled-components'
import {TabsType} from 'antd/lib/tabs'

const TabBar = styled.span`
  display: inline-block;
  width: 80px;
  text-align: center;
`

const BasicTabs: React.FC<{
  style?: object
  type?: TabsType
  tabs: string[]
  onChange?: any
}> = ({style = {}, type = 'card', tabs, onChange, children = []}) => {
  return (
    <Tabs type={type} style={{marginTop: 50, ...style}} onChange={onChange}>
      {tabs.map((tab, idx) => {
        return (
          <Tabs.TabPane tab={<TabBar>{tab}</TabBar>} key={tab}>
            {React.Children.toArray(children)[idx]}
          </Tabs.TabPane>
        )
      })}
    </Tabs>
  )
}

export default BasicTabs
