import React from 'react'
import {observer} from 'mobx-react-lite'
import {useHistory} from 'react-router-dom'
import {DatePicker, Form, Input, Select} from 'antd'
import BasicLayout from '../../components/Layout/BasicLayout'
import TitlePanel from '../../components/Layout/TitlePanel'
import Content from '../../components/Layout/Content'
import ServerSearch from '../../components/Search/ServerSearch'
import BasicTabs from '../../components/Tabs/Tabs'
import StyledButton from '../../components/Button/StyledButton'
import {MinusCircleOutlined} from '@ant-design/icons'
import {useStores} from '../../stores'

const NoticePage = () => {
  const history = useHistory()
  const {
    adminStore: {adminServerList},
    messageStore,
  } = useStores()
  const onFinish = (values: any) => {
    messageStore
      .addNotice({
        board: values.board,
        textNotice: values.textNotice.map((notice: any) => {
          return {
            title: notice.title,
            createdAt: notice.createdAt.format(),
            target: '전체유저',
            body: notice.body,
          }
        }),
      })
      .then(() => {
        alert('공지사항이 등록되었습니다.')
        history.go(0)
      })
  }
  return (
    <BasicLayout>
      <TitlePanel>메시지 관리</TitlePanel>
      <Content title={'전체 공지 및 조회'}>
        <ServerSearch />
        <BasicTabs tabs={['전체공지', '공지 내역']}>
          <div style={{width: 800, marginLeft: 70}}>
            <Form
              requiredMark={false}
              colon={false}
              initialValues={{
                board: 'hello',
                textNotice: [{target: '전체유저'}],
              }}
              labelCol={{span: 4}}
              wrapperCol={{span: 20}}
              onFinish={onFinish}
            >
              <div style={{textAlign: 'right', marginBottom: 10}}>
                <StyledButton blue>전체 공지 발송</StyledButton>
              </div>
              <Form.Item
                label={'공지 형태'}
                name={'board'}
                style={{marginBottom: 10}}
              >
                <Select style={{width: 150}}>
                  <Select.Option key={'hello'} value="hello">
                    로그인 전(hello)
                  </Select.Option>
                  {adminServerList.map((worldServer) => {
                    const value = `world-${worldServer._id}`
                    return (
                      <Select.Option key={value} value={value}>
                        {value}
                      </Select.Option>
                    )
                  })}
                </Select>
              </Form.Item>
              <Form.List name="textNotice">
                {(fields, {add, remove}) => (
                  <>
                    <div style={{textAlign: 'right', marginBottom: 10}}>
                      <StyledButton type={'button'} green onClick={add}>
                        +공지 항목 추가
                      </StyledButton>
                    </div>
                    {fields.map(({key, ...field}) => {
                      return (
                        <div key={key}>
                          <Form.Item
                            {...field}
                            label={'제목'}
                            name={[field.name, 'title']}
                            fieldKey={[field.fieldKey, 'title']}
                            rules={[
                              {required: true, message: '제목을 입력해주세요.'},
                            ]}
                          >
                            <Input placeholder="제목" />
                          </Form.Item>
                          <Form.Item
                            {...field}
                            label={'시간'}
                            name={[field.name, 'createdAt']}
                            fieldKey={[field.fieldKey, 'createdAt']}
                            style={{marginBottom: 10}}
                            rules={[
                              {required: true, message: '시간을 선택해주세요.'},
                            ]}
                          >
                            <DatePicker showTime style={{width: 150}} />
                          </Form.Item>
                          <div style={{width: '100%', display: 'flex'}}>
                            <Form.Item
                              {...field}
                              name={[field.name, 'body']}
                              fieldKey={[field.fieldKey, 'body']}
                              labelCol={{span: 4}}
                              wrapperCol={{span: 19}}
                              label={'공지'}
                              style={{flex: 1}}
                              rules={[
                                {
                                  required: true,
                                  message: '공지 내용을 입력해주세요.',
                                },
                              ]}
                            >
                              <Input.TextArea size={'large'} />
                            </Form.Item>
                            <MinusCircleOutlined
                              style={{float: 'right'}}
                              onClick={() => remove(field.name)}
                            />
                          </div>
                        </div>
                      )
                    })}
                  </>
                )}
              </Form.List>
            </Form>
          </div>
          <div>2</div>
        </BasicTabs>
      </Content>
    </BasicLayout>
  )
}

export default observer(NoticePage)
