import React, {useState} from 'react'
import {Table, Tabs} from 'antd'
import BasicTabs from '../Tabs/Tabs'
import {observer} from 'mobx-react-lite'
import {Tiers} from './Equipments'

const HeroType = Tiers

const Heros: React.FC<any> = ({items = []}) => {
  const [activeKey, setActiveKey] = useState(HeroType[0])
  const columns = [
    {title: '넘버', dataIndex: 'number'},
    {title: '강화등급', dataIndex: 'increasedTier'},
    {title: '강화', dataIndex: 'upgrade'},
    {title: '전투력', dataIndex: 'battlePower'},
    {title: '레벨', dataIndex: 'level'},
    {title: '속성', dataIndex: 'elemental'},
  ]

  return (
    <div>
      <Tabs defaultActiveKey="1" onChange={() => {}}>
        <Tabs.TabPane tab="보유 영웅" key="보유 영웅">
          <BasicTabs
            type={'line'}
            tabs={HeroType}
            style={{marginTop: 0}}
            onChange={(activeKey: string) => setActiveKey(activeKey)}
          />
          <div>
            <Table
              columns={columns}
              dataSource={items
                .filter(
                  (item: any) =>
                    item.identity === HeroType.indexOf(activeKey) + 1
                )
                .map((item: any, idx: number) => ({
                  ...item,
                  number: idx + 1,
                }))}
            />
          </div>
        </Tabs.TabPane>
      </Tabs>
    </div>
  )
}

export default observer(Heros)
