import React, {useState} from 'react'
import {Table, Tabs} from 'antd'
import BasicTabs from '../Tabs/Tabs'
import {observer} from 'mobx-react-lite'

const ItemType = [
  '열쇠',
  '상자',
  '증표',
  '보석',
  '부스터',
  '티켓',
  '재료 아이템(일반)',
  '무기조각',
]
const Items: React.FC<any> = ({items = []}) => {
  const [activeKey, setActiveKey] = useState(ItemType[0])
  const columns = [
    {title: '넘버', dataIndex: 'number'},
    {title: '아이템 이름', dataIndex: 'label'},
    {title: '보유 수량', dataIndex: 'amount'},
    {title: '비고', dataIndex: 'etc'},
  ]

  return (
    <div>
      <Tabs defaultActiveKey="1" onChange={() => {}}>
        <Tabs.TabPane tab="보유 아이템" key="보유 아이템">
          <BasicTabs
            type={'line'}
            tabs={ItemType}
            style={{marginTop: 0}}
            onChange={(activeKey: string) => setActiveKey(activeKey)}
          />
          <div>
            <Table
              columns={columns}
              dataSource={items
                .filter(
                  (item: any) => item.type === ItemType.indexOf(activeKey) + 1
                )
                .map((item: any, idx: number) => ({
                  ...item,
                  number: idx + 1,
                }))}
            />
          </div>
        </Tabs.TabPane>
      </Tabs>
    </div>
  )
}

export default observer(Items)
