import {makeAutoObservable} from 'mobx'
import moment from 'moment'
import {command} from '../../apis/server'

export default class ServerModel {
  store: any
  key: string
  number: number
  _id: string
  url: string
  status: string
  startedAt: string
  commandAt: string
  playerNum: number
  cpu: number
  mem: number
  aliveUntil: string
  constructor(store: any, {_id, playerNum}: any, number: number) {
    makeAutoObservable(this)
    const {
      startedAt,
      url,
      status,
      cpu,
      mem,
      aliveUntil,
      commandAt,
    } = store.servers[`world-${_id}`]
    this.store = store
    this._id = _id
    this.number = number
    this.key = _id
    this.url = url
    this.status = status
    this.playerNum = playerNum
    this.cpu = cpu
    this.mem = mem
    this.startedAt = moment(startedAt).format('YYYY.MM.DD HH:mm:ss')
    this.commandAt = moment(commandAt).format('YYYY.MM.DD HH:mm:ss')
    this.aliveUntil = moment(aliveUntil).format('YYYY.MM.DD HH:mm:ss')
  }

  start = () => {
    return command(this._id, 'start')
  }

  stop = () => {
    return command(this._id, 'stop')
  }
}

export const columns = []
