import {makeAutoObservable} from 'mobx'
import moment from 'moment'
import {removeAdminUser} from '../../apis/admin_user'

export default class AdminUserModel {
  store: any
  key: string
  _id: string
  number: number
  grade: string
  part: string
  country: string
  createdAt: string
  lastLoggedInAt: string
  constructor(
    store: any,
    {_id, grade, props, createdAt, lastLoggedInAt}: any,
    number: number
  ) {
    makeAutoObservable(this)
    this.store = store
    this._id = _id
    this.number = number
    this.key = _id
    this.grade = grade
    this.part = props.part
    this.country = props.country
    this.createdAt = moment(createdAt).format('YYYY.MM.DD HH:mm:ss')
    this.lastLoggedInAt = moment(lastLoggedInAt).format('YYYY.MM.DD HH:mm:ss')
  }

  remove = () => {
    return removeAdminUser(this._id)
  }
}

export const columns = []
