import React from 'react'
import {Descriptions} from 'antd'

const ChannelDesc: React.FC<{channel: any}> = ({channel = {}}) => {
  return (
    <Descriptions bordered>
      <Descriptions.Item label="채널 서버">{channel.name}</Descriptions.Item>
      <Descriptions.Item label="채널 가입자(명)">
        {channel.count}
      </Descriptions.Item>
    </Descriptions>
  )
}

export default ChannelDesc
