export const getPageInfo = (
  totalSize: number,
  page: number,
  size: number = 20,
  countPagePerPageGroup = 5
) => {
  const maxPage = Math.ceil(totalSize / size) || 1
  const startPage =
    (Math.ceil(page / countPagePerPageGroup) - 1) * countPagePerPageGroup + 1
  const endPage =
    startPage + countPagePerPageGroup - 1 < maxPage
      ? startPage + countPagePerPageGroup - 1
      : maxPage
  const prevPerPage = startPage !== 1 ? startPage - 1 : null
  const nextPerPage = endPage < maxPage ? endPage + 1 : null
  const prevPage = page - 1 === 0 ? null : page - 1
  const nextPage = maxPage - page === 0 ? null : page + 1
  const pages = totalSize
    ? Array.from(new Array(endPage - startPage + 1), (_, i) => startPage + i)
    : []

  return {
    page,
    pages,
    maxPage,
    prevPerPage,
    nextPerPage,
    prevPage,
    nextPage,
    size,
    totalSize,
  }
}
