import React from 'react'
import {Descriptions, Button} from 'antd'
import {DownOutlined} from '@ant-design/icons'
import PlayerModel, {PlayerType} from '../../stores/models/PlayerModel'
import {observer} from 'mobx-react-lite'

const PlayerDesc: React.FC<{player: PlayerModel}> = ({player}) => {
  const {
    _id,
    country,
    lang,
    createdAt,
    nickname,
    userLevel,
    mark,
    changePlayerType,
  } = player
  const {typeInfo = {}}: any = mark

  const getTypeInfoUpdatedAt = (type: string) => {
    return typeInfo.type === type ? typeInfo.updatedAt : ''
  }

  return (
    <Descriptions
      bordered
      size={'small'}
      labelStyle={{width: 150}}
      style={{width: '90%'}}
    >
      <Descriptions.Item label="플레이어ID">{_id}</Descriptions.Item>
      <Descriptions.Item label="서비스 국가">{country}</Descriptions.Item>
      <Descriptions.Item label="현재 언어">{lang}</Descriptions.Item>
      <Descriptions.Item label="생성일">{createdAt}</Descriptions.Item>
      <Descriptions.Item label="닉네임">{nickname}</Descriptions.Item>
      <Descriptions.Item label="계정레벨">{userLevel}</Descriptions.Item>
      <Descriptions.Item
        label={<span style={{color: 'red'}}>블랙 리스트 지정</span>}
      >
        <div style={{color: 'red'}}>
          <Button
            style={{borderRadius: 5, marginRight: 10}}
            size={'small'}
            onClick={() => changePlayerType(PlayerType.블랙리스트)}
          >
            <DownOutlined />
          </Button>
          {getTypeInfoUpdatedAt(PlayerType.블랙리스트)}
        </div>
      </Descriptions.Item>
      <Descriptions.Item
        label={<span style={{color: '#1190ff'}}>테스터 지정</span>}
      >
        <div style={{color: '#1190ff'}}>
          <Button
            style={{borderRadius: 5, marginRight: 10}}
            size={'small'}
            onClick={() => changePlayerType(PlayerType.테스터)}
          >
            <DownOutlined />
          </Button>
          {getTypeInfoUpdatedAt(PlayerType.테스터)}
        </div>
      </Descriptions.Item>
      <Descriptions.Item
        label={<span style={{color: '#e3a717'}}>VIP 지정</span>}
      >
        <div style={{color: '#e3a717'}}>
          <Button
            style={{borderRadius: 5, marginRight: 10}}
            size={'small'}
            onClick={() => changePlayerType(PlayerType.VIP)}
          >
            <DownOutlined />
          </Button>
          {getTypeInfoUpdatedAt(PlayerType.VIP)}
        </div>
      </Descriptions.Item>
      <Descriptions.Item label={<span style={{color: 'red'}}>계정 정지</span>}>
        <div style={{color: 'red'}}>
          {getTypeInfoUpdatedAt(PlayerType.계정정지)}
        </div>
      </Descriptions.Item>
      <Descriptions.Item
        label={<span style={{color: '#c763f8'}}>계정 해지</span>}
      >
        <div style={{color: '#c763f8'}}>
          {getTypeInfoUpdatedAt(PlayerType.계정해지)}
        </div>
      </Descriptions.Item>
    </Descriptions>
  )
}

export default observer(PlayerDesc)
