import {axiosApi} from '../utils/axios'

export const getNotices = () => {
  return axiosApi({url: '/notices/show', method: 'get'})
}

/**
 * board: hello =>
 * board: world
 * @param params
 */
export const upsertNotice = (params: any) => {
  return axiosApi({url: '/notices/upsert', method: 'put', params})
}
