import {axiosApi} from '../utils/axios'

type PlayerQuery = {
  wid: string
  cid: string
  pid: string
  projection?: string
}

interface MainType {
  wid?: string
  cid?: string
  zid?: string
  gid?: string
  pids?: string
  content: any
}

export const getPlayers = (params: any) => {
  return axiosApi({url: '/players/findMany', method: 'get', params})
}

export const getPlayer = (params: PlayerQuery) => {
  params.projection = 'profile,data'
  return axiosApi({url: '/players/findOne', method: 'get', params})
}

export const changePlayerMark = (params: any) => {
  return axiosApi({url: '/players/changeMark', method: 'put', params})
}

export const sendMail = (params: MainType) => {
  return axiosApi({url: '/players/sendMail', method: 'post', params})
}
