import React, {useEffect} from 'react'
import {Card, Form, Input, Button, Checkbox} from 'antd'
import {useHistory} from 'react-router-dom'
import CenterLayout from '../components/Layout/CenterLayout'
import {LockOutlined, UserOutlined} from '@ant-design/icons'
import {axiosApi} from '../utils/axios'
import {useStores} from '../stores'
import {observer} from 'mobx-react-lite'

const LoginPage = () => {
  const [form] = Form.useForm()
  const {appStore} = useStores()
  const history = useHistory()
  const {
    userInfo: {id, remember},
  } = appStore
  const onFinish = (values: any) => {
    axiosApi({
      url: '/auth/login',
      method: 'get',
      params: {
        id: values.id,
        pwd: values.pwd,
        secret: process.env.REACT_APP_SECRET,
      },
    }).then(({data}) => {
      appStore.login({id: values.id, remember: values.remember}, data.token)
      history.replace('/admin/user')
    })
  }

  useEffect(() => {
    form.setFieldsValue({id, remember})
  }, [remember])
  return (
    <CenterLayout style={{background: '#2367A8', textAlign: 'center'}}>
      <div>
        <h2 style={{color: '#fff', fontWeight: 'normal'}}>
          운영자 관리 시스템
        </h2>
        <Card
          style={{
            width: 300,
            background: '#fafafa',
            borderRadius: 5,
            padding: 5,
          }}
          bodyStyle={{padding: 10}}
        >
          <div
            style={{
              fontSize: 14,
              width: 100,
              margin: '10px auto 20px',
              color: '#0082FF',
              padding: '0 10px 5px',
              borderBottom: '2px solid #0082FF',
            }}
          >
            도메인 계정
          </div>
          <div>
            <Form
              form={form}
              name="normal_login"
              className="login-form"
              onFinish={onFinish}
            >
              <Form.Item
                name="id"
                rules={[
                  {required: true, message: 'Please input your Username!'},
                ]}
              >
                <Input
                  prefix={<UserOutlined style={{color: '#c6c6c6'}} />}
                  placeholder="아이디"
                />
              </Form.Item>
              <Form.Item
                name="pwd"
                rules={[
                  {required: true, message: 'Please input your Password!'},
                ]}
              >
                <Input
                  prefix={<LockOutlined style={{color: '#c6c6c6'}} />}
                  type="password"
                  placeholder="암호"
                />
              </Form.Item>
              <Form.Item
                name="remember"
                valuePropName="checked"
                style={{textAlign: 'left'}}
              >
                <Checkbox>아이디 저장</Checkbox>
              </Form.Item>
              <Form.Item>
                <Button
                  block
                  type="primary"
                  htmlType="submit"
                  className="login-form-button"
                >
                  로그인
                </Button>
              </Form.Item>
            </Form>
          </div>
        </Card>
      </div>
    </CenterLayout>
  )
}

export default observer(LoginPage)
