import React, {useEffect, useState} from 'react'
import {observer} from 'mobx-react-lite'
import BasicLayout from '../../components/Layout/BasicLayout'
import TitlePanel from '../../components/Layout/TitlePanel'
import Content from '../../components/Layout/Content'
import ServerSearch from '../../components/Search/ServerSearch'
import BasicTabs from '../../components/Tabs/Tabs'
import Title from '../../components/Text/Title'
import {DatePicker, Space, Table, TimePicker} from 'antd'
import PlayerSearch from '../../components/Search/PlayerSearch'
import {getChats} from '../../apis/chat'
import moment from 'moment'
import StyledButton from '../../components/Button/StyledButton'

const ChatPage = () => {
  const [chats, setChats] = useState([])
  const [channel, setChannel] = useState('')
  const [date, setDate] = useState(moment().format('YYYY.MM.DD'))
  const [time, setTime] = useState('00:00:00')
  const columns = [
    {title: '넘버', dataIndex: 'number'},
    {
      title: '채팅 날짜',
      dataIndex: 'ticks',
      render: (v: string) => moment(v).format('YYYY.MM.DD'),
    },
    {
      title: '채팅 시간',
      dataIndex: 'ticks',
      render: (v: string) => moment(v).format('HH:mm:ss'),
    },
    {title: '채팅 내용', dataIndex: 'content', render: (v: any) => v.text},
    {title: '비고', dataIndex: ''},
  ]

  const getChatList = (params: any = {}) => {
    return getChats({
      rid: channel,
      roomType: 'channel',
      sortAsc: true,
      ...params,
    }).then(({data}) => {
      setChats(
        data.map((datum: any, idx: number) => ({number: idx + 1, ...datum}))
      )
    })
  }

  const getChatListByPID = (pid: string) => {
    return getChatList({pid})
  }

  const getChatListByTime = () => {
    const startTicks = moment(`${date} ${time}`).valueOf()
    return getChatList({startTicks})
  }

  useEffect(() => {}, [])
  return (
    <BasicLayout>
      <TitlePanel>메시지 관리</TitlePanel>
      <Content>
        <ServerSearch onChange={(w: any, c: any) => c && setChannel(c._id)} />
        <Title style={{marginTop: 40}}>채팅 조회</Title>
        <BasicTabs tabs={['플레이어', '날짜 시간']}>
          <div>
            <div style={{marginBottom: 20}}>
              <PlayerSearch
                onlyPid
                onSearch={(searchType, searchValue) => {
                  getChatListByPID(searchValue)
                }}
              />
            </div>
            <Table bordered columns={columns} dataSource={chats} />
          </div>
          <div>
            <Space size={14} style={{marginBottom: 20}}>
              <Space style={{marginRight: 30}}>
                채팅 날짜
                <DatePicker
                  onChange={(date: any, dateString: string) =>
                    setDate(dateString)
                  }
                />
              </Space>
              <Space>
                채팅 시간
                <TimePicker
                  onChange={(time: any, timeString: string) =>
                    setTime(timeString)
                  }
                  defaultValue={moment('00:00:00', 'HH:mm:ss')}
                />
              </Space>
              <StyledButton blue onClick={getChatListByTime}>
                검색
              </StyledButton>
            </Space>
            <Table bordered columns={columns} dataSource={chats} />
          </div>
        </BasicTabs>
      </Content>
    </BasicLayout>
  )
}

export default observer(ChatPage)
