import {makeAutoObservable} from 'mobx'
import PlayerModel from './models/PlayerModel'
import {getPlayer, getPlayers} from '../apis/player'
import {getPageInfo} from '../utils/page'

export default class PlayerStore {
  currentPlayer: PlayerModel | undefined
  playerList: PlayerModel[]
  wid: string
  cid: string
  playerCount: number | undefined
  pageInfo: any
  constructor() {
    makeAutoObservable(this)
    this.currentPlayer = undefined
    this.playerList = []
    this.playerCount = undefined
    this.wid = ''
    this.cid = ''
    this.pageInfo = getPageInfo(0, 1, 5, 10)
  }

  setCurrentPlayer = (page: number) => {
    this.currentPlayer = this.playerList[page]
  }

  setPlayerList = (players: PlayerModel[]) => {
    this.playerList = players.map(
      (datum: PlayerModel, idx: number) =>
        new PlayerModel(
          this,
          datum,
          (this.pageInfo.page - 1) * this.pageInfo.size + idx + 1
        )
    )
    this.setCurrentPlayer(0)
  }

  getPlayer = (query: any) => {
    if (query.pid) {
      return getPlayer({wid: this.wid, cid: this.cid, pid: query.pid}).then(
        ({data}) => {
          this.pageInfo = getPageInfo(data ? 1 : 0, 1, this.pageInfo.size, 10)
          return this.setPlayerList(data ? [data] : [])
        }
      )
    }
    return this.getPlayerList('', 1)
  }

  getPlayerList = async (
    type?: string,
    page: number = this.pageInfo.page,
    params: any = {}
  ) => {
    let {query = {}, ...restParams} = params
    if (!this.wid || !this.cid) {
      return false
    }
    if (type && type !== '플레이어') {
      query = {
        'mark.typeInfo.type': {$eq: type},
      }
    }

    await this.getPlayerCount({query, ...restParams})
    return getPlayers({
      wid: this.wid,
      cid: this.cid,
      justCount: false,
      projection: 'profile',
      query,
      skip: (page - 1) * this.pageInfo.size,
      limit: this.pageInfo.size,
      ...restParams,
    }).then(({data}) => {
      this.pageInfo = getPageInfo(
        this.playerCount || 0,
        page,
        this.pageInfo.size,
        10
      )
      this.setPlayerList(data)
    })
  }

  getPlayerCount = (params = {}) => {
    return getPlayers({
      wid: this.wid,
      cid: this.cid,
      justCount: true,
      ...params,
    }).then(({data}) => {
      this.playerCount = data.count
    })
  }

  setIds = (w: any, c: any) => {
    this.wid = w?._id
    this.cid = c?._id
  }
}
