import {axiosApi} from '../utils/axios'
import axios from 'axios'

export const getHelloServer = () => {
  return axios.get('https://dr-game.ex4games.com:30001/hello')
}

export const getServers = () => {
  return axiosApi({url: '/servers/show', method: 'get'})
}

export const getWorldServers = () => {
  return axiosApi({url: '/servers/showWorlds', method: 'get'})
}

export const getChannels = (wid: string) => {
  return axiosApi({url: '/servers/showChannels', method: 'get', params: {wid}})
}

export const createChannel = () => {
  return axiosApi({url: '/servers/createChannel', method: 'put'})
}

export const command = (target: string, command: string) => {
  return axiosApi({
    url: '/servers/command',
    method: 'put',
    params: {target, command},
  })
}
