import React, {useEffect, useState} from 'react'
import {observer} from 'mobx-react-lite'
import {Popover, Space, Table, Tabs} from 'antd'
import styled from 'styled-components'
import BasicLayout from '../../components/Layout/BasicLayout'
import TitlePanel from '../../components/Layout/TitlePanel'
import Content from '../../components/Layout/Content'
import ServerSearch from '../../components/Search/ServerSearch'
import StyledButton from '../../components/Button/StyledButton'
import {useStores} from '../../stores'
import PlayerModel, {PlayerType} from '../../stores/models/PlayerModel'
import {CloseSquareOutlined, MailOutlined} from '@ant-design/icons'
import MailForm from '../../components/Form/MailForm'

const TabBar = styled.span`
  display: inline-block;
  width: 80px;
  text-align: center;
`

const PlayersPage = () => {
  const [activeKey, setActiveKey] = useState('')
  const [visible, setVisible] = useState(false)
  const [selectedRowModels, setSelectedRowModels] = useState<PlayerModel[]>([])
  const {
    playerStore: {wid, cid, getPlayerList, playerList, playerCount, pageInfo},
  } = useStores()

  const changeTypeModels = (type: PlayerType | string) => {
    return Promise.all(
      selectedRowModels.map((p) => p.changePlayerType(type))
    ).then(() => getPlayerList(activeKey))
  }

  const Buttons = () => {
    return (
      <>
        <Space style={{marginBottom: 10}}>
          <Popover
            placement="bottomRight"
            content={
              <div
                style={{
                  width: 600,
                  border: '2px solid #bababb',
                }}
              >
                <div
                  style={{
                    background: '#fafafa',
                    display: 'flex',
                    padding: '10px 20px 0',
                    alignItems: 'center',
                    justifyContent: 'space-between',
                  }}
                >
                  <Space align={'baseline'}>
                    <MailOutlined
                      style={{
                        fontSize: 20,
                        color: '#a7a7a7',
                        marginRight: 5,
                      }}
                    />
                    <h2>우편 보내기</h2>
                  </Space>
                  <span>
                    <CloseSquareOutlined
                      onClick={() => setVisible(false)}
                      style={{fontSize: 20, color: '#a7a7a7'}}
                    />
                  </span>
                </div>
                <div style={{padding: '0 16px 20px'}}>
                  <MailForm
                    players={selectedRowModels}
                    onClose={() => setVisible(false)}
                  />
                </div>
              </div>
            }
            trigger="click"
            visible={visible}
            onVisibleChange={setVisible}
          >
            <StyledButton style={{width: 120}} purple>
              우편 보내기
            </StyledButton>
          </Popover>
          <StyledButton
            style={{width: 120}}
            gray
            onClick={() => changeTypeModels(PlayerType.계정정지)}
          >
            계정 정지
          </StyledButton>
          <StyledButton
            style={{width: 120}}
            gray
            onClick={() => changeTypeModels(PlayerType.계정해지)}
          >
            계정 해지
          </StyledButton>
          <StyledButton
            style={{width: 120}}
            yellow
            onClick={() => changeTypeModels(PlayerType.VIP)}
          >
            VIP 지정
          </StyledButton>
          <StyledButton yellow onClick={() => changeTypeModels('')}>
            VIP 해제
          </StyledButton>
        </Space>
        <div style={{float: 'right', marginRight: 10}}>
          전체 플레이어 {playerCount}명
        </div>
      </>
    )
  }

  const columns = [
    {title: '넘버', dataIndex: 'number'},
    {title: 'PID', dataIndex: 'pid'},
    {title: '닉네임', dataIndex: 'nickname'},
    {title: '생성 날짜', dataIndex: 'createdAt'},
    {title: '레벨', dataIndex: 'userLevel'},
    {
      title: '비고',
      dataIndex: 'mark',
      render: ({typeInfo}: any) =>
        typeInfo?.type && `${typeInfo.type} 지정 ${typeInfo.updatedAt || ''}`,
    },
  ]

  useEffect(() => {
    if (wid && cid) {
      getPlayerList(undefined, 1)
    }
  }, [wid, cid])

  useEffect(() => {
    if (wid && cid) {
      getPlayerList(activeKey, 1)
    }
  }, [activeKey])

  return (
    <BasicLayout>
      <TitlePanel>회원 관리</TitlePanel>
      <Content>
        <ServerSearch />
        <Tabs
          type={'card'}
          style={{marginTop: 50}}
          onChange={(activeKey) => setActiveKey(activeKey)}
        >
          <Tabs.TabPane tab={<TabBar>플레이어</TabBar>} key="플레이어" />
          <Tabs.TabPane tab={<TabBar>테스터</TabBar>} key="테스터" />
          <Tabs.TabPane tab={<TabBar>블랙리스트</TabBar>} key="블랙리스트" />
          <Tabs.TabPane tab={<TabBar>계정정지</TabBar>} key="계정정지" />
          <Tabs.TabPane tab={<TabBar>VIP</TabBar>} key="VIP" />
        </Tabs>
        <div>
          <Buttons />
          <Table
            rowKey={'pid'}
            bordered
            columns={columns}
            dataSource={playerList}
            pagination={{
              position: ['bottomCenter'],
              total: playerCount,
              pageSize: pageInfo.size,
              onChange: (page) => {
                getPlayerList(activeKey, page)
              },
            }}
            rowSelection={{
              onChange: (
                selectedRowKeys: React.Key[],
                selectedRowModels: PlayerModel[]
              ) => {
                setSelectedRowModels(selectedRowModels)
              },
            }}
          />
        </div>
      </Content>
    </BasicLayout>
  )
}

export default observer(PlayersPage)
