import {makeAutoObservable} from 'mobx'
import {upsertNotice} from '../apis/notice'

export default class MessageStore {
  servers: any
  constructor() {
    makeAutoObservable(this)
    this.servers = {}
  }

  addNotice = (data: any) => {
    return upsertNotice({
      board: data.board,
      notice: {
        textNotice: data.textNotice,
      },
    })
  }
}
