import React from 'react'
import styled, {css} from 'styled-components'

type StyledButtonProps = {
  purple?: boolean
  blue?: boolean
  gray?: boolean
  yellow?: boolean
  green?: boolean
}

const StyledButton = styled.button<StyledButtonProps>`
  color: #fff;
  box-shadow: 0 2px 0 rgb(0 0 0 / 5%);
  text-shadow: 0 -1px 0 rgb(0 0 0 / 12%);
  border-radius: 5px;
  text-align: center;
  padding: 4px 15px;
  cursor: pointer;
  outline: none !important;
  ${({purple}) =>
    purple &&
    css`
      background: #c763f8;
      border: 1px solid #c454f8;
      &:focus {
        border: 1px solid #c454f8;
      }
    `}
  ${({blue}) =>
    blue &&
    css`
      background: #1190ff;
      border: 1px solid #007eff;
    `}
  ${({gray}) =>
    gray &&
    css`
      background: #98a0ab;
      border: 1px solid #9099a5;
    `}
  ${({yellow}) =>
    yellow &&
    css`
      background: #e3a717;
      border: 1px solid #e09c00;
    `}
  ${({green}) =>
    green &&
    css`
      background: #43a678;
      border: 1px solid #00b377;
    `}
`

export default StyledButton
