import React, {useEffect, useState} from 'react'
import {InputNumber, Select, Space} from 'antd'

type OptionsType = {label: string; value: string}[]
export interface ProductOptionsType {
  label: string
  value: string
  options?: OptionsType
  optionType?: string
}

const RewardItem: React.FC<{
  productOptions: ProductOptionsType[]
  placeholder?: string
  onChange?: any
  noCount?: boolean
}> = ({
  productOptions = [],
  placeholder,
  onChange = (product: any, count: any) => {},
  noCount = false,
}) => {
  const [count, setCount] = useState<number>(0)
  const [product, setProduct] = useState<string>('')

  useEffect(() => {
    product && onChange(product, count)
  }, [product, count])
  return (
    <div style={{marginBottom: 5}}>
      <Space>
        <Select
          onChange={(v: string) => setProduct(v)}
          placeholder={placeholder}
          style={{width: 300}}
        >
          {productOptions.map((product: any, idx: number) => {
            const value = String(product.value)
            return (
              <Select.Option key={value || idx} value={value}>
                {product.label}
              </Select.Option>
            )
          })}
        </Select>
        {!noCount && (
          <Space>
            <InputNumber value={count} onChange={(v) => setCount(v)} />
            <span>수량</span>
          </Space>
        )}
      </Space>
    </div>
  )
}

export default RewardItem
