import {makeAutoObservable} from 'mobx'
import moment from 'moment'
import {changePlayerMark} from '../../apis/player'
import {stores} from '../index'

export enum PlayerType {
  블랙리스트 = '블랙리스트',
  테스터 = '테스터',
  VIP = 'VIP',
  계정정지 = '계정정지',
  계정해지 = '계정해지',
}

type Mark = {
  typeInfo?: {
    type: PlayerType
    updatedAt: string
  }
}

export default class PlayerModel {
  store: any
  pid: string
  _id: string
  number: number
  wid: string
  mark: Mark
  nickname: string
  country: string
  lang: string
  userLevel: string
  userBattlePower: string
  createdAt: string
  userResources: any
  gameItems: any
  equipments: any
  heros: any
  adventures: any
  agit: any
  constructor(
    store: any,
    {_id, wid, mark = {}, profile, data, createdAt}: any,
    idx: number
  ) {
    makeAutoObservable(this)
    this.store = store
    this._id = _id
    this.wid = wid
    this.number = idx
    this.pid = _id
    this.mark = mark
    this.country = '한국'
    this.lang = '한국어'
    this.nickname = profile.nickname
    this.userLevel = profile.userLevel
    this.userBattlePower = profile.userBattlePower
    this.userResources = data.userResources
    this.adventures = {
      number: 1,
      key: 1,
      progressedChapterZone: data.progressedChapterZone,
      progressedClearStageCount: data.progressedClearStageCount,
      accumulatedClearStageCount: data.accumulatedClearStageCount,
      towerClearCount: data.towerClearCount,
    }
    this.agit = [
      {
        key: '노말',
        number: 1,
        name: '노말',
        progress: data.agitChapterZoneNormal,
      },
      {
        key: '하드',
        number: 2,
        name: '하드',
        progress: data.agitChapterZoneHard,
      },
      {
        key: '엘리트',
        number: 3,
        name: '엘리트',
        progress: data.agitChapterZoneElite,
      },
      {
        key: '드래곤',
        number: 4,
        name: '드래곤',
        progress: data.agitChapterZoneDragon,
      },
      {
        key: '던전-지하감옥',
        number: 5,
        name: '던전-지하감옥',
        progress: data.infinityChapterZone,
      },
    ]

    this.gameItems = data?.gameItems
      ? Object.keys(data.gameItems).map((itemKey: any, idx: number) => {
          return {
            ...stores.appStore.gameItemData[itemKey],
            ...data.gameItems[itemKey],
          }
        })
      : []
    this.heros = data?.heros
      ? Object.keys(data.heros).map((itemKey: any, idx: number) => {
          return {
            ...stores.appStore.heroData[itemKey],
            ...data.heros[itemKey],
          }
        })
      : []
    this.equipments = data?.equipments
      ? Object.keys(data.equipments).map((itemKey: any, idx: number) => {
          const equipment = data.equipments[itemKey]
          return {
            ...stores.appStore.equipmentKeyList[equipment.key],
            ...data.equipments[itemKey],
          }
        })
      : []
    this.createdAt = moment(createdAt).format('YYYY.MM.DD HH:mm:ss')

    if (this.mark?.typeInfo?.updatedAt) {
      this.mark.typeInfo.updatedAt = moment(mark.typeInfo.updatedAt).format(
        'YYYY.MM.DD HH:mm:ss'
      )
    }
  }

  changePlayerType = (type: PlayerType | string) => {
    if (this.mark?.typeInfo?.type === type) {
      type = ''
    }
    return this.changePlayerMark({
      typeInfo: {type, updatedAt: moment().format()},
    })
  }

  changePlayerMark = (mark: any) => {
    return changePlayerMark({
      wid: this.wid,
      pid: this.pid,
      mark: {...this.mark, ...mark},
    }).then(() => {
      if (mark?.typeInfo?.updatedAt) {
        mark.typeInfo.updatedAt = moment(mark.typeInfo.updatedAt).format(
          'YYYY.MM.DD HH:mm:ss'
        )
      }
      this.mark = {...this.mark, ...mark}
    })
  }
}
