import React, {useState} from 'react'
import {Table, Tabs} from 'antd'
import BasicTabs from '../Tabs/Tabs'
import {observer} from 'mobx-react-lite'

const ItemType = [
  '무기',
  '갑옷',
  '방패',
  '장갑',
  '신발',
  '반지',
  '목걸이',
  '코스튬',
]

type StatType = {
  AttackPow: '공격력'
  Defence: '방어력'
  MaxHp: '최대Hp'
  AttackSpeed: '공격속도'
  MoveSpeed: '이동속도'
  CriticalChance: '치명타확률'
  CriticalDamage: '치명타배율'
  MpRegen: '스킬마나리젠율'
  MaxMp: '최대마나'
}

const Stats = [
  '공격력',
  '방어력',
  '최대Hp',
  '공격속도',
  '이동속도',
  '치명타확률',
  '치명타배율',
  '스킬마나리젠율',
  '최대마나',
]

export const Tiers = ['일반', '고급', '희귀', '영웅', '전설', '초월', '신화']

const Equipments: React.FC<any> = ({items = []}) => {
  const [activeKey, setActiveKey] = useState(ItemType[0])
  const columns = [
    {title: '넘버', dataIndex: 'number'},
    {title: '재화 종류', dataIndex: 'label'},
    {title: '등급', dataIndex: 'tier', render: (v: any) => Tiers[v]},
    {title: '강화', dataIndex: 'currentUpgrade'},
    {
      title: '기본 능력',
      dataIndex: 'stat',
      render: (v: any, item: any) => {
        return `${Stats[item.mainStatType]} ${item.mainStatValue}`
      },
    },
    {
      title: '옵션',
      dataIndex: 'enchantData',
      render: (v: any = {}, item: any) => {
        return Object.keys(v).map((statKey: any, idx: number) => (
          <div key={idx}>{`${Stats[statKey]} ${v[statKey]}`}</div>
        ))
      },
    },
    {title: '판매가격(골드)', dataIndex: 'sellPrice'},
  ]

  return (
    <div>
      <Tabs defaultActiveKey="1" onChange={() => {}}>
        <Tabs.TabPane tab="보유 장비" key="보유 장비">
          <BasicTabs
            type={'line'}
            tabs={ItemType}
            style={{marginTop: 0}}
            onChange={(activeKey: string) => setActiveKey(activeKey)}
          />
          <div>
            <Table
              columns={columns}
              dataSource={items
                .filter(
                  (item: any) => item.type === ItemType.indexOf(activeKey) + 1
                )
                .map((item: any, idx: number) => ({
                  ...item,
                  number: idx + 1,
                }))}
            />
          </div>
        </Tabs.TabPane>
      </Tabs>
    </div>
  )
}

export default observer(Equipments)
