import React from 'react'
import {Layout, Menu} from 'antd'
import {CloudOutlined, HomeOutlined} from '@ant-design/icons'
import './basicSider.less'
import {useStores} from '../../stores'
import {observer} from 'mobx-react-lite'
import {Link} from 'react-router-dom'

const routers = [
  {
    key: 'admin',
    title: '관리자 관리',
    icon: <HomeOutlined />,
    menuItems: [
      {key: 'user', label: '관리자 계정 관리'},
      {key: 'server', label: '서버 관리'},
    ],
  },
  {
    key: 'player',
    title: '유저 관리',
    icon: <CloudOutlined />,
    menuItems: [
      {key: 'one', label: '특정 플레이어 조회'},
      {key: 'many', label: '전체 플레이어 조회'},
    ],
  },
  {
    key: 'message',
    title: '메시지 관리',
    icon: <CloudOutlined />,
    menuItems: [
      {key: 'notice', label: '전체 공지 및 조회'},
      {key: 'mail', label: '우편 발송 및 조회'},
      {key: 'chat', label: '채팅 조회'},
    ],
  },
  {
    key: 'product',
    title: '상품 관리',
    icon: <CloudOutlined />,
    menuItems: [
      {key: 'paid', label: '유료 상품 조회'},
      {key: 'game', label: '게임 상품 조회'},
    ],
  },
  {
    key: 'data',
    title: '게임 데이터 관리',
    icon: <CloudOutlined />,
    menuItems: [
      {key: 'rank', label: '게임 랭킹 조회'},
      {key: 'reward', label: '컨텐츠별 보상 조회'},
    ],
  },
  {
    key: 'stats',
    title: '데이터 통계',
    icon: <CloudOutlined />,
    menuItems: [
      {key: 'product', label: '유료 상품 현황'},
      {key: 'player', label: '플레이어 활동 현황'},
    ],
  },
]

const BasicSider: React.FC = () => {
  const {appStore} = useStores()
  const {isCollapsed} = appStore

  return (
    <Layout.Sider collapsed={isCollapsed}>
      <div>
        <div className="ant-pro-sider-logo" id="logo">
          <a>
            <img src="/static/logo.f0355d39.svg" alt="logo" />
            <h1>운영자 관리 시스템</h1>
          </a>
        </div>
        <Menu
          mode="inline"
          theme="dark"
          defaultOpenKeys={routers.reduce((acc: any[], curr) => {
            if (curr.key !== 'product' && curr.key !== 'stats')
              acc.push(curr.key)
            return acc
          }, [])}
        >
          {routers.map((router) => {
            const {key, title, icon, menuItems} = router
            return (
              <Menu.SubMenu key={key} icon={icon} title={title}>
                {menuItems.map((menuItem) => {
                  const {label} = menuItem
                  const url = `/${key}/${menuItem.key}`
                  return (
                    <Menu.Item key={url}>
                      <Link to={url}>{label}</Link>
                    </Menu.Item>
                  )
                })}
              </Menu.SubMenu>
            )
          })}
        </Menu>
      </div>
    </Layout.Sider>
  )
}

export default observer(BasicSider)
