import {axiosApi} from '../utils/axios'

export const getAdminUsers = (params = {}) => {
  return axiosApi({url: '/accounts/show', method: 'get'})
}

export const createAdminUser = (params: any) => {
  return axiosApi({url: '/accounts/create', method: 'post', params})
}

export const changeAdminUser = () => {
  return axiosApi({url: '/accounts/change', method: 'patch'})
}

export const removeAdminUser = (id: string) => {
  return axiosApi({url: '/accounts/delete', method: 'delete', params: {id}})
}
