import React, {useEffect, useState} from 'react'
import {Select, Space} from 'antd'
import Title from '../Text/Title'
import {getChannels} from '../../apis/server'
import {useStores} from '../../stores'
import {observer} from 'mobx-react-lite'

const ServerSearch = ({onChange = (w: any, c: any) => {}}) => {
  const [selectedWorldServer, setSelectedWorldServer] = useState<any>('')
  const [channels, setChannels] = useState<any>([])
  const [selectedChannel, setSelectedChannel] = useState('')
  const {
    adminStore: {servers, getAdminWorldServerList, adminServerList},
    playerStore: {setIds, wid, cid},
  } = useStores()

  useEffect(() => {
    getAdminWorldServerList().then((data) => {
      setSelectedWorldServer(wid || data[0]?._id)
    })
  }, [])

  useEffect(() => {
    if (selectedWorldServer) {
      getChannels(selectedWorldServer).then(({data}) => {
        setChannels(data)
        setSelectedChannel(cid || data[0]?._id)
      })
    }
  }, [selectedWorldServer])

  useEffect(() => {
    setIds(
      adminServerList.find((s: any) => s._id === selectedWorldServer),
      channels.find((c: any) => c._id === selectedChannel)
    )
    onChange(
      adminServerList.find((s: any) => s._id === selectedWorldServer),
      channels.find((c: any) => c._id === selectedChannel)
    )
  }, [selectedWorldServer, selectedChannel])

  return (
    <Space>
      <Title style={{marginRight: 15}}>서버 선택</Title>
      <Select
        style={{width: 200}}
        value={selectedWorldServer}
        onChange={(v) => setSelectedWorldServer(v)}
      >
        {adminServerList.map((world: any) => (
          <Select.Option key={world._id} value={world._id}>
            {world._id}
          </Select.Option>
        ))}
      </Select>
      <Select
        style={{width: 200}}
        value={selectedChannel}
        onChange={(v) => setSelectedChannel(v)}
      >
        {channels.map((channel: any) => (
          <Select.Option key={channel._id} value={channel._id}>
            {channel._id}
          </Select.Option>
        ))}
      </Select>
      <span style={{display: 'inline-block', marginLeft: 10}}>
        월드 서버 주소
      </span>
      <span style={{display: 'inline-block', marginLeft: 10}}>
        {servers &&
          selectedWorldServer &&
          servers[`world-${selectedWorldServer}`]?.url}
      </span>
    </Space>
  )
}

export default observer(ServerSearch)
