import {makeAutoObservable} from 'mobx'
import AdminUserModel from './models/AdminUserModel'
import {getAdminUsers, createAdminUser} from '../apis/admin_user'
import {
  getChannels,
  getHelloServer,
  getServers,
  getWorldServers,
} from '../apis/server'
import ServerModel from './models/ServerModel'
import ChannelModel from './models/ChannelModel'

export default class AdminStore {
  adminUserList: AdminUserModel[]
  adminServerList: ServerModel[]
  adminChannelList: ChannelModel[]
  servers: any
  constructor() {
    makeAutoObservable(this)
    this.adminUserList = []
    this.adminServerList = []
    this.adminChannelList = []
    this.servers = {}
  }

  getServers = async () => {
    const {
      data: {
        servers: {worlds},
      },
    } = await getHelloServer()
    return getServers().then(({data}) => {
      this.servers = data.reduce((acc: any, curr: any) => {
        acc[curr._id] = {...curr, url: worlds[curr._id.split('-')[1]]}
        return acc
      }, {})
    })
  }

  getAdminUserList = () => {
    return getAdminUsers().then(({data}) => {
      this.adminUserList = data.map(
        (datum: AdminUserModel, idx: number) =>
          new AdminUserModel(this, datum, idx + 1)
      )
    })
  }

  createAdminUser = (type: string, data: any) => {
    return createAdminUser({
      grade: type,
      id: data._id,
      pwd: data.pwd,
      props: {
        part: data.part,
        country: data.country,
      },
    })
  }

  getAdminWorldServerList = async () => {
    await this.getServers()
    return getWorldServers().then(({data}) => {
      this.adminServerList = data.map(
        (datum: ServerModel, idx: number) =>
          new ServerModel(this, datum, idx + 1)
      )
      return data
    })
  }

  getAdminChannelList = async (wid: string) => {
    return getChannels(wid).then(({data}) => {
      this.adminChannelList = data.map(
        (datum: ChannelModel, idx: number) =>
          new ChannelModel(this, datum, idx + 1)
      )
    })
  }
}
