import React, {useEffect, useState} from 'react'
import {observer} from 'mobx-react-lite'
import {Form, Space, Tabs, Table} from 'antd'
import BasicLayout from '../../components/Layout/BasicLayout'
import TitlePanel from '../../components/Layout/TitlePanel'
import Content from '../../components/Layout/Content'
import StyledButton from '../../components/Button/StyledButton'
import {useStores} from '../../stores'
import ServerSearch from '../../components/Search/ServerSearch'
import ServerModel from '../../stores/models/ServerModel'
import ChannelDesc from '../../components/Description/ChannelDesc'
import {getPlayers} from '../../apis/player'
import BasicTabs from '../../components/Tabs/Tabs'
import ChannelModel from '../../stores/models/ChannelModel'

const ServerPage = () => {
  const [channel, setChannel] = useState<any>()
  const [selectedRowModels, setSelectedRowModels] = useState<ServerModel[]>([])
  const [selectedChannelModels, setSelectedChannelModels] = useState<
    ChannelModel[]
  >([])
  const {
    adminStore: {
      getAdminWorldServerList,
      getAdminChannelList,
      adminServerList,
      adminChannelList,
    },
  } = useStores()
  const columns = [
    {title: '넘버', dataIndex: 'number'},
    {title: '서버명', dataIndex: '_id'},
    {title: '서버주소', dataIndex: 'url'},
    {title: '서버상태', dataIndex: 'status'},
    {title: '접속자 기준', dataIndex: 'playerNum'},
    {title: '정지 일시', dataIndex: 'aliveUntil'},
    {title: '시작 일시', dataIndex: 'startedAt'},
    {title: '비고', dataIndex: ''},
  ]

  const onStart = () => {
    return Promise.all(selectedRowModels.map((model) => model.start())).then(
      getAdminWorldServerList
    )
  }

  const onStop = () => {
    return Promise.all(selectedRowModels.map((model) => model.stop())).then(
      getAdminWorldServerList
    )
  }

  useEffect(() => {
    getAdminWorldServerList()
  }, [])
  return (
    <BasicLayout>
      <TitlePanel>서버 관리</TitlePanel>
      <Content>
        <ServerSearch
          onChange={(w: any, c: any) => {
            if (c?._id) {
              getPlayers({wid: w._id, cid: c._id, justCount: true}).then(
                ({data}) => {
                  setChannel({
                    name: c._id,
                    count: data.count,
                    ...c,
                  })
                }
              )
            }
          }}
        />
        <div style={{marginTop: 20}}>
          <ChannelDesc channel={channel} />
        </div>
        <BasicTabs
          tabs={['월드 서버', '채널 서버']}
          onChange={(activeKey: string) => {
            if (activeKey.indexOf('채널 서버') > -1) {
              getAdminChannelList(channel.wid)
            }
          }}
        >
          <div>
            <Space style={{marginLeft: 5, marginBottom: 20}}>
              <StyledButton purple onClick={onStart}>
                서버 시작
              </StyledButton>
              <StyledButton blue onClick={onStop}>
                서버 정지
              </StyledButton>
            </Space>
            <Table
              bordered
              columns={columns}
              dataSource={adminServerList}
              pagination={{position: ['bottomCenter']}}
              rowSelection={{
                hideSelectAll: true,
                onChange: (
                  selectedRowKeys: React.Key[],
                  selectedRowModels: ServerModel[]
                ) => {
                  setSelectedRowModels(selectedRowModels)
                },
              }}
            />
          </div>
          <div>
            <Space style={{marginLeft: 5, marginBottom: 20}}>
              <StyledButton gray onClick={() => {}}>
                서버 통합
              </StyledButton>
            </Space>
            <Table
              bordered
              columns={columns}
              dataSource={adminChannelList}
              pagination={{position: ['bottomCenter']}}
              rowSelection={{
                hideSelectAll: true,
                onChange: (
                  selectedRowKeys: React.Key[],
                  selectedRowModels: ChannelModel[]
                ) => {
                  setSelectedChannelModels(selectedRowModels)
                },
              }}
            />
          </div>
        </BasicTabs>
      </Content>
    </BasicLayout>
  )
}

export default observer(ServerPage)
