import React from 'react'
import {Card} from 'antd'

const Content: React.FC<{title?: string}> = ({children, title = null}) => {
  return (
    <Card bodyStyle={{padding: '10px 20px'}} style={{marginTop: 5}}>
      {title && <h3>{title}</h3>}
      {children}
    </Card>
  )
}

export default Content
